window.adev = window.adev || {};

(function ($) {
	"use strict";

	adev = {
		init: function() {
			
		},
		ajaxGet: function(url){
			return $.ajax({
				url: url,
				type: 'GET',
			})
		},
		ajaxPost: function(url, data){
			return $.ajax({
				url: url,
				type: 'POST',
				data: data
			})
		}
	}

	adev.init();

}(jQuery));